import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

Vue.config.productionTip = false

import Home from './components/Home'
import WomensFellowship from './components/WomensFellowship'
import SundaySchool from './components/SundaySchool'
import YouthFellowship from './components/YouthFellowship'
import History from './components/History'
import WhosWho from './components/WhosWho'

import WorshipCalendar from './components/WorshipCalendar'
import Gallery from './components/Gallery'
import Notices from './components/Notices'
import Contact from './components/Contact'

//libs
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'

//registering router
Vue.use(VueRouter)

//Creating routes
const router = new VueRouter({
  mode:'hash',
  base:__dirname,
  routes: [
    { path:'/', component:Home },
    { path:'/womens-fellowship', component:WomensFellowship },
    { path:'/sunday-school', component:SundaySchool },
    { path:'/youth-fellowship', component:YouthFellowship },
    { path:'/history', component:History },
    { path:'/whos-who', component:WhosWho },
    { path:'/worship-calendar', component:WorshipCalendar },
    { path:'/gallery', component:Gallery },
    { path:'/notices', component:Notices },
    { path:'/contact', component:Contact }
  ],
  scrollBehavior () {
    return { x: 0, y: 0 }
  }
})

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
