            <p>
                Holy Trinity Church is one of the oldest churches in India and is located in the historic city of AURANGABAD in the Maharashtra State, home to the world famous AJANTA & ELLORA Caves. Holy Trinity Church is affiliated to the Marathwada Diocese of the CHURCH OF NORTH INDIA and is the only English Language Church in the Marathwada Diocese.
            </p>

            <!-- <h3>Some of the activities of the women are:</h3> -->

            <h4>History :</h4>
            <p>
                Holy Trinity Church was constructed in the early 1800’s by the British Indian Army and served as a Garrison Church for many years to come. As per the existing church records the earliest Divine service was conducted by Rev Little ( The first Chaplain) in the year 1865 . Initially HTC was under the
Calcutta Diocese of the Church of England which was established in the year 1813 and was the the only diocese in India . Subsequently two more dioceses were created , Madras in 1835 and Bombay in 1837. The Anglican dioceses in North India merged with the United Church of North India ( Congregationalist and Presbyterian ), the Methodist Church ( British and Australian Conferences) ,the Council of Baptist Churches in Northern India, the Church of the Brethren in India and the Disciples of Christ to form the CHURCH OF NORTH INDIA in the year 1970. From 1970 to 2000 Holy Trinity Church was part of the Nashik Diocese . In the year 2000 Holy Trinity Church became part of the newly created Marathwada Diocese.
            </p>
            <p>
                In the last more than 150 years of its existence Holy Trinity Church has had a
very glorious and illustrious history with few parallels in the history of
Christainity in India . Many a stalwart have been part of the Holy Trinity Family
and have contributed whole heartedly in the development of the church.
            </p>

