var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"women-fellowship"},[_c('div',{staticClass:"container"},[_c('h2',[_vm._v("Women's Fellowship")]),_c('blockquote',{staticClass:"blockquote"},[_c('p',{staticClass:"mb-0"},[_vm._v("\n                    \"A new commandment I give to you, that you love one another: just as I have loved you, you also are to love one another.  By this all people will know that you are my disciples, if you have\n                        love for one another.\"\n                ")]),_c('span',{staticClass:"bq-footer"},[_vm._v("                    \n                    -John 13:34-35\n                ")])]),_c('p',[_vm._v("\n                The Women’s Fellowship of Holy Trinity Church is the women’s ministry of the Church which\nprovides a platform for women to learn and grow in the knowledge of Christ, have loving fellowship\nwith each other, and participate in the mission of the Church. The focus is centred on learning and\ngrowing, outreach as well as social concerns.\n            ")]),_c('h3',[_vm._v("Some of the activities of the women are")]),_c('h4',[_vm._v("Bible Study")]),_c('p',[_vm._v("\n                The women meet regularly every month for praise and prayer, meditation and study of the scriptures.\nMeetings are held once a month in the house of one of the members and besides studying the Word, it\nhelps to provide encouragement to each other and offer prayers and support to those in need. Different\nthemes are selected every year by the women. Some of the themes completed include Women of the\nBible, Minor Prophets, Parables of Jesus, Ten Commandments and Fruits of the Spirit.\n            ")]),_c('h4',[_vm._v("Outreach")]),_c('p',[_vm._v("\n                The women’s group organise visits to rural churches for Church engagement programmes wherein\nmembers take part in the village Church services and spend time with the members to understand their\nneeds and also provide prayer support for the members. Some of the Churches visited are in the\nvillages of Rahatgaon, Dhakephal, Bajajnagar and Waluj. Floor mats were provided to the churches as\nper their need.\n            ")]),_c('h4',[_vm._v("Social Activities")]),_c('p',[_vm._v("\n                As part of faith in action, the women’s fellowship regularly visits orphanages and women shelters and\ndistribute provisions as well as blankets, school uniforms and school bags depending on the need\nexpressed. Hearing aids were presented to few children in a School for the Hearing and Speaking\nImpaired. The sick in hospital and in homes are visited and support is provided through prayers and\nfellowship. During the Christmas season, food packets are distributed among the poor and sick found\non the streets and in government hospitals. Annual visits are arranged to the Missionaries of Charity\nin Aurangabad during the Christmas season and provisions, clothes, cakes and sweets are distributed.\n            ")]),_c('h4',[_vm._v("Special Sundays")]),_c('p',[_vm._v("\n                The Women’s Fellowship participate in the Church services on special Sundays celebrating Mother’s\nDay and Women’s day. The women present skits, songs and special messages by women speakers.\nDuring the Christmas season, the women actively participate by presenting a specially written and\ndirected Christmas Skit.\n\nMembership is open to all women of faith who is a full communicant of the Church. Minimal monthly\nsubscription is collected to organise the various activities.\n            ")]),_c('p',{staticStyle:{"margin-top":"45px"}},[_vm._v("\n                The Executive Board consists of the President, Secretary and Treasurer who are elected periodically as per the rules of the Church.\n            ")]),_c('h5',{staticStyle:{"font-weight":"bold","text-align":"center"}},[_vm._v("\n                Monthly Meet : 4 th Saturday of every month at 6.00 pm "),_c('br'),_vm._v("\n                President (2016 to till date):  Mrs. Anita Mitra\n            ")])])])}]

